/* global document, Office, console */

Office.onReady((info) => {
  document.getElementById("qrdiv").style.display = "none";
  document.getElementById("imdiv").style.display = "none";
  document.getElementById("cediv").style.display = "none";
  if (info.host === Office.HostType.PowerPoint) {
    // Traduções
    let UIText = UIStrings.getLocaleStrings(Office.context.displayLanguage);
    for (const value of [
      "Greeting",
      "Introduction",
      "Certificado",
      "QRCode",
      "Imagens",
      "Texto1",
      "Site1",
      "Botao1",
      "Texto2",
      "Botao2",
      "Texto3",
    ]) {
      document.getElementById(value).innerText = UIText[value];
    }
    document.getElementById("Botao1").onclick = qrcode;
  }
});

// QR Code
export async function qrcode() {
  const url = "https://album.med.br/addin/qrcode.php?u=" + document.getElementById("Site1").value;
  let xhttp = new XMLHttpRequest();
  xhttp.onreadystatechange = function () {
    if (xhttp.readyState !== 4) {
      return;
    }
    if (xhttp.status == 200) {
      //let dados = JSON.parse(xhttp.responseText);
      Office.context.document.setSelectedDataAsync(xhttp.responseText, {
        coercionType: Office.CoercionType.XmlSvg,
        imageLeft: 830,
        imageTop: 410,
        imageWidth: 100,
      });
    } else if (xhttp.status == 404) {
      console.log(xhttp.responseText);
    } else {
      console.error({
        status: xhttp.status,
        statusText: xhttp.statusText,
      });
    }
  };
  xhttp.open("GET", url, true);
  xhttp.send();
}

const UIStrings = (function () {
  "use strict";

  const UIStrings = {};
  // English
  UIStrings.EN = {
    Greeting: "Welcome",
    Introduction: "Discover what you can do here!",
    Certificado: "Create certificates.",
    QRCode: "Include custom QR Codes.",
    Imagens: "Search and add AlbumMed images.",
    Texto1: "Click the button to insert a QR Code.",
    Site1: "Type the website:",
    Botao1: "Insert QR Code",
    Texto2: "Search for Images.",
    Botao2: "Go",
    Texto3: "Generate Certificates",
  };
  // Português
  UIStrings.BR = {
    Greeting: "Benvindo",
    Introduction: "Descubra o que pode fazer aqui!",
    Certificado: "Crie certificados.",
    QRCode: "Inclua QR Codes customizados.",
    Imagens: "Busque e adicione imagens AlbumMed.",
    Texto1: "Clique no botão abaixo para inserir um QR Code.",
    Site1: "Digite o site:",
    Botao1: "Inserir QR Code",
    Texto2: "Busca por Imagens.",
    Botao2: "Ir",
    Texto3: "Gerar Certificados",
  };
  UIStrings.getLocaleStrings = function (locale) {
    let text;
    switch (locale) {
      case "en-US":
        text = UIStrings.EN;
        break;
      case "pt-BR":
        text = UIStrings.BR;
        break;
      default:
        text = UIStrings.EN;
        break;
    }
    return text;
  };
  return UIStrings;
})();
